      <ul class="list-disc list-inside space-y-2 pl-2">
        <li>顶部分类标签可以快速切换不同类型的加密算法，点击时会自动展开对应分类</li>
        <li>分类面板采用手风琴式设计，确保界面整洁，一次只能展开一个分类</li>
        <li>搜索框支持模糊搜索，可根据算法名称或描述快速查找算法</li>
        <li>选中的算法卡片会有以下视觉反馈：
          <ul class="list-disc list-inside pl-4 mt-1">
