    <div>
      <h4 class="text-md font-semibold mb-2">支持的算法类型</h4>
      <div class="space-y-4">
        <div>
          <h5 class="font-medium text-primary dark:text-primary-400">编码/解码</h5>
          <ul class="list-disc list-inside pl-4">
            <li><strong>Base64</strong>：基础的编码方式，非加密算法，常用于传输二进制数据</li>
          </ul>
        </div>
        
        <div>
          <h5 class="font-medium text-primary dark:text-primary-400">对称加密</h5>
          <ul class="list-disc list-inside pl-4">
            <li><strong>AES</strong>：高级加密标准，最常用的对称加密算法，支持128/192/256位密钥（16/24/32字符）</li>
            <li><strong>DES</strong>：数据加密标准，较早的对称加密算法，安全性低于AES</li>
            <li><strong>RC4</strong>：一种流加密算法，实现简单，但存在安全隐患</li>
          </ul>
        </div>
        
        <div>
          <h5 class="font-medium text-primary dark:text-primary-400">哈希算法</h5>
          <p class="text-sm italic mb-2">注意：哈希算法是单向的，不可逆，只能用于验证，无法解密</p>
          <ul class="list-disc list-inside pl-4">
            <li><strong>MD5</strong>：常用的哈希算法，输出32位十六进制字符串</li>
            <li><strong>SHA1</strong>：安全哈希算法1，输出40位十六进制字符串</li>
            <li><strong>SHA256</strong>：安全哈希算法2，输出64位十六进制字符串</li>
            <li><strong>SHA512</strong>：安全哈希算法2，输出128位十六进制字符串</li>
            <li><strong>HMAC-SHA256</strong>：密钥相关的哈希消息认证码，结合密钥和SHA256算法</li>
          </ul>
        </div>
        
        <div>
          <h5 class="font-medium text-primary dark:text-primary-400">特殊功能</h5>
          <ul class="list-disc list-inside pl-4">
