    <div>
      <h4 class="text-md font-semibold mb-2">加密模式说明</h4>
      <p class="mb-2">对于AES和DES加密，支持以下加密模式：</p>
      <ul class="list-disc list-inside pl-4">
        <li><strong>ECB</strong>：电子密码本模式，最简单的加密模式，但安全性较低</li>
        <li><strong>CBC</strong>：密码块链接模式，每个明文块先与前一个密文块进行异或后，再进行加密</li>
        <li><strong>CFB</strong>：密码反馈模式，将上一个密文块加密后与当前明文块异或得到当前密文块</li>
        <li><strong>OFB</strong>：输出反馈模式，类似于CFB，但使用加密算法的输出而不是密文进行反馈</li>
        <li><strong>CTR</strong>：计数器模式，将递增的计数器进行加密，再与明文异或得到密文</li>
      </ul>
    </div>

    <div>
      <h4 class="text-md font-semibold mb-2">注意事项</h4>
