        <li>对称加密算法（AES、DES、RC4）在加密和解密时必须使用相同的密钥</li>
        <li>哈希算法是单向的，无法解密，只能用于验证</li>
        <li>JWT解码功能仅用于查看JWT令牌的内容，不会验证签名的有效性</li>
        <li>为保证安全，请勿在公共设备上处理敏感信息</li>
      </ul>
