/**
 * 文本加密解密服务
 * 提供多种加密算法实现，包括Base64、AES、DES、RC4、MD5、SHA等
 * 
 * 使用说明：
 * 1. 编码/解码类：base64Encode, base64Decode - 用于文本的编码和解码
 * 2. 对称加密类：aesEncrypt/aesDecrypt, desEncrypt/desDecrypt, rc4Encrypt/rc4Decrypt - 需要密钥
 * 3. 哈希算法类：md5Hash, sha1Hash, sha256Hash, sha512Hash - 单向哈希，不可逆
 * 4. HMAC类：hmacSha256 - 需要密钥的哈希算法
 * 5. 特殊功能：decodeJwt - 解析JWT令牌
 */

// 动态导入CryptoJS，减少初始加载时间
let CryptoJS = null;

/**
 * 加载CryptoJS库
 * @returns {Promise} CryptoJS库实例
 */
async function loadCryptoJS() {
  if (!CryptoJS) {
    const module = await import('crypto-js');
    CryptoJS = module.default || module;
  }
  return CryptoJS;
}

/**
 * Base64编码
 * @param {string} str 要编码的字符串
 * @returns {string} Base64编码后的字符串
 */
export function base64Encode(str) {
  return btoa(encodeURIComponent(str).replace(/%([0-9A-F]{2})/g, (_, p1) =>
    String.fromCharCode(parseInt(p1, 16))
  ));
}

/**
 * Base64解码
 * @param {string} str Base64编码的字符串
 * @returns {string} 解码后的字符串
 */
export function base64Decode(str) {
  try {
    // 处理JWT中的base64url格式
    const base64 = str.replace(/-/g, '+').replace(/_/g, '/');
    let padding = '';
    if (base64.length % 4 !== 0) {
      padding = '='.repeat(4 - (base64.length % 4));
    }
    
    return decodeURIComponent(
      atob(base64 + padding)
        .split('')
        .map(c => '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2))
        .join('')
    );
  } catch (e) {
    console.error('Base64解码错误:', e);
    throw new Error('解码失败');
  }
}

/**
 * Base64Url转Base64
 * @param {string} str Base64Url编码的字符串
 * @returns {string} 标准Base64编码的字符串
 */
export function base64UrlToBase64(str) {
  let output = str.replace(/-/g, '+').replace(/_/g, '/');
  switch (output.length % 4) {
    case 0: break;
    case 2: output += '=='; break;
    case 3: output += '='; break;
    default: break;
  }
  return output;
}

/**
 * JWT解码
 * @param {string} token JWT令牌
 * @returns {Object} 解码后的JWT对象，包含header、payload和signature
 */
export function decodeJwt(token) {
  if (!token) return { header: 'null', payload: 'null', signature: '', error: '请输入JWT令牌' };
  token = token.trim().replace(/\s+/g, '');
  const parts = token.split('.');
  if (parts.length !== 3) return { header: 'null', payload: 'null', signature: '', error: '无效的JWT格式，应包含三部分(header.payload.signature)' };
  
  let header = '';
  let payload = '';
  let headerError = false;
  let payloadError = false;
  
  try {
    const decodedHeader = JSON.parse(base64Decode(parts[0]));
    header = JSON.stringify(decodedHeader, null, 2);
  } catch {
    header = 'null';
    headerError = true;
  }
  
  try {
    const decodedPayload = JSON.parse(base64Decode(parts[1]));
    payload = JSON.stringify(decodedPayload, null, 2);
  } catch {
    payload = 'null';
    payloadError = true;
  }
  
  const signature = parts[2] || '';
  const error = headerError && payloadError ? 'JWT解析失败，Base64解码错误' : 
                headerError ? 'Header解析失败' : 
                payloadError ? 'Payload解析失败' : '';
  
  return { header, payload, signature, error };
}

/**
 * AES加密
 * 高级加密标准（Advanced Encryption Standard），是一种对称加密算法
 * @param {string} text 要加密的文本
 * @param {string} key 密钥
 * @param {Object} options 加密选项
 * @returns {Promise<string>} 加密后的字符串
 */
export async function aesEncrypt(text, key, options = {}) {
  if (!key) throw new Error('请输入密钥');
  
  // 验证密钥长度
  if (![16, 24, 32].includes(key.length)) {
    throw new Error('密钥长度必须是16、24或32个字符');
  }
  
  const crypto = await loadCryptoJS();
  const defaultOptions = {
    mode: 'ECB',
    padding: 'Pkcs7'
  };
  
  const config = { ...defaultOptions, ...options };
  const keyObj = crypto.enc.Utf8.parse(key);
  
  try {
    const encrypted = crypto.AES.encrypt(text, keyObj, {
      mode: crypto.mode[config.mode],
      padding: crypto.pad[config.padding]
    });
    return encrypted.toString();
  } catch (e) {
    console.error('AES加密错误:', e);
    throw new Error('加密失败: ' + e.message);
  }
}

/**
 * AES解密
 * @param {string} cipherText 加密的文本
 * @param {string} key 密钥
 * @param {Object} options 解密选项
 * @returns {Promise<string>} 解密后的字符串
 */
export async function aesDecrypt(cipherText, key, options = {}) {
  if (!key) throw new Error('请输入密钥');
  
  // 验证密钥长度
  if (![16, 24, 32].includes(key.length)) {
    throw new Error('密钥长度必须是16、24或32个字符');
  }
  
  const crypto = await loadCryptoJS();
  const defaultOptions = {
    mode: 'ECB',
    padding: 'Pkcs7'
  };
  
  const config = { ...defaultOptions, ...options };
  const keyObj = crypto.enc.Utf8.parse(key);
  
  try {
    const bytes = crypto.AES.decrypt(cipherText, keyObj, {
      mode: crypto.mode[config.mode],
      padding: crypto.pad[config.padding]
    });
    const decrypted = bytes.toString(crypto.enc.Utf8);
    
    if (!decrypted) {
      throw new Error('解密失败：密钥可能不正确');
    }
    
    return decrypted;
  } catch (e) {
    console.error('AES解密错误:', e);
    throw new Error('解密失败: ' + e.message);
  }
}

/**
 * DES加密
 * 数据加密标准（Data Encryption Standard），是一种对称加密算法
 * @param {string} text 要加密的文本
 * @param {string} key 密钥
 * @param {Object} options 加密选项
 * @returns {Promise<string>} 加密后的字符串
 */
export async function desEncrypt(text, key, options = {}) {
  if (!key) throw new Error('请输入密钥');
  
  const crypto = await loadCryptoJS();
  const defaultOptions = {
    mode: 'ECB',
    padding: 'Pkcs7'
  };
  
  const config = { ...defaultOptions, ...options };
  const keyObj = crypto.enc.Utf8.parse(key);
  
  try {
    const encrypted = crypto.DES.encrypt(text, keyObj, {
      mode: crypto.mode[config.mode],
      padding: crypto.pad[config.padding]
    });
    return encrypted.toString();
  } catch (e) {
    console.error('DES加密错误:', e);
    throw new Error('加密失败: ' + e.message);
  }
}

/**
 * DES解密
 * @param {string} cipherText 加密的文本
 * @param {string} key 密钥
 * @param {Object} options 解密选项
 * @returns {Promise<string>} 解密后的字符串
 */
export async function desDecrypt(cipherText, key, options = {}) {
  if (!key) throw new Error('请输入密钥');
  
  const crypto = await loadCryptoJS();
  const defaultOptions = {
    mode: 'ECB',
    padding: 'Pkcs7'
  };
  
  const config = { ...defaultOptions, ...options };
  const keyObj = crypto.enc.Utf8.parse(key);
  
  try {
    const bytes = crypto.DES.decrypt(cipherText, keyObj, {
      mode: crypto.mode[config.mode],
      padding: crypto.pad[config.padding]
    });
    const decrypted = bytes.toString(crypto.enc.Utf8);
    
    if (!decrypted) {
      throw new Error('解密失败：密钥可能不正确');
    }
    
    return decrypted;
  } catch (e) {
    console.error('DES解密错误:', e);
    throw new Error('解密失败: ' + e.message);
  }
}

/**
 * RC4加密
 * 一种流加密算法，密钥长度可变
 * @param {string} text 要加密的文本
 * @param {string} key 密钥
 * @returns {Promise<string>} 加密后的字符串
 */
export async function rc4Encrypt(text, key) {
  if (!key) throw new Error('请输入密钥');
  
  const crypto = await loadCryptoJS();
  
  try {
    const encrypted = crypto.RC4.encrypt(text, key);
    return encrypted.toString();
  } catch (e) {
    console.error('RC4加密错误:', e);
    throw new Error('加密失败: ' + e.message);
  }
}

/**
 * RC4解密
 * @param {string} cipherText 加密的文本
 * @param {string} key 密钥
 * @returns {Promise<string>} 解密后的字符串
 */
export async function rc4Decrypt(cipherText, key) {
  if (!key) throw new Error('请输入密钥');
  
  const crypto = await loadCryptoJS();
  
  try {
    const bytes = crypto.RC4.decrypt(cipherText, key);
    const decrypted = bytes.toString(crypto.enc.Utf8);
    
    if (!decrypted) {
      throw new Error('解密失败：密钥可能不正确');
    }
    
    return decrypted;
  } catch (e) {
    console.error('RC4解密错误:', e);
    throw new Error('解密失败: ' + e.message);
  }
}

/**
 * MD5哈希
 * 一种常用的哈希算法，输出32位十六进制字符串，不可逆
 * @param {string} text 要哈希的文本
 * @returns {Promise<string>} MD5哈希值
 */
export async function md5Hash(text) {
  const crypto = await loadCryptoJS();
  return crypto.MD5(text).toString();
}

/**
 * SHA1哈希
 * 安全哈希算法1，输出40位十六进制字符串，不可逆
 * @param {string} text 要哈希的文本
 * @returns {Promise<string>} SHA1哈希值
 */
export async function sha1Hash(text) {
  const crypto = await loadCryptoJS();
  return crypto.SHA1(text).toString();
}

/**
 * SHA256哈希
 * 安全哈希算法2，输出64位十六进制字符串，不可逆
 * @param {string} text 要哈希的文本
 * @returns {Promise<string>} SHA256哈希值
 */
export async function sha256Hash(text) {
  const crypto = await loadCryptoJS();
  return crypto.SHA256(text).toString();
}

/**
 * SHA512哈希
 * 安全哈希算法2，输出128位十六进制字符串，不可逆
 * @param {string} text 要哈希的文本
 * @returns {Promise<string>} SHA512哈希值
 */
export async function sha512Hash(text) {
  const crypto = await loadCryptoJS();
  return crypto.SHA512(text).toString();
}

/**
 * HMAC-SHA256哈希
 * 密钥相关的哈希消息认证码，结合密钥和SHA256算法
 * @param {string} text 要哈希的文本
 * @param {string} key 密钥
 * @returns {Promise<string>} HMAC-SHA256哈希值
 */
export async function hmacSha256(text, key) {
  if (!key) throw new Error('请输入密钥');
  
  const crypto = await loadCryptoJS();
  return crypto.HmacSHA256(text, key).toString();
}

/**
 * 获取所有支持的加密算法
 * @returns {Array} 支持的加密算法列表
 */
export function getSupportedAlgorithms() {
  return [
    { value: 'base64', name: 'Base64', type: 'encoding', needsKey: false, description: '基础的编码方式，非加密算法，常用于传输二进制数据' },
    { value: 'aes', name: 'AES（高级加密标准）', type: 'symmetric', needsKey: true, description: '最常用的对称加密算法，支持128/192/256位密钥' },
    { value: 'des', name: 'DES（数据加密标准）', type: 'symmetric', needsKey: true, description: '较早的对称加密算法，密钥长度较短，安全性低于AES' },
    { value: 'rc4', name: 'RC4（流加密）', type: 'symmetric', needsKey: true, description: '一种流加密算法，实现简单，但存在安全隐患' },
    { value: 'md5', name: 'MD5', type: 'hash', needsKey: false, description: '常用的哈希算法，输出32位十六进制字符串，不可逆' },
    { value: 'sha1', name: 'SHA1', type: 'hash', needsKey: false, description: '安全哈希算法1，输出40位十六进制字符串，不可逆' },
    { value: 'sha256', name: 'SHA256', type: 'hash', needsKey: false, description: '安全哈希算法2，输出64位十六进制字符串，不可逆' },
    { value: 'sha512', name: 'SHA512', type: 'hash', needsKey: false, description: '安全哈希算法2，输出128位十六进制字符串，不可逆' },
    { value: 'hmacSha256', name: 'HMAC-SHA256', type: 'hmac', needsKey: true, description: '密钥相关的哈希消息认证码，结合密钥和SHA256算法' },
    { value: 'jwt', name: 'JWT 解码', type: 'special', needsKey: false, description: '解析JWT令牌的头部、载荷和签名部分' }
  ];
}

export default {
  base64Encode,
  base64Decode,
  base64UrlToBase64,
  decodeJwt,
  aesEncrypt,
  aesDecrypt,
  desEncrypt,
  desDecrypt,
  rc4Encrypt,
  rc4Decrypt,
  md5Hash,
  sha1Hash,
  sha256Hash,
  sha512Hash,
  hmacSha256,
  getSupportedAlgorithms
}; 