<template>
  <div 
    class="border-2 rounded-lg transition-all duration-300 cursor-pointer overflow-hidden relative group"
    :class="[
      isActive 
        ? 'border-primary bg-primary/10 dark:bg-primary/20 shadow-md -translate-y-0.5 z-[1] ring-2 ring-primary/30 ring-opacity-50' 
        : 'border-neutral-200 dark:border-neutral-700 shadow-sm hover:shadow hover:-translate-y-0.5',
      compact ? 'p-2' : 'p-4'
    ]"
    @click="$emit('select')"
  >
    <div :class="['flex items-center justify-between', compact ? 'mb-1' : 'mb-2']">
      <div class="flex items-center flex-grow min-w-0">
        <div :class="[
            'rounded-full flex items-center justify-center mr-3 transition-all duration-300',
            isActive ? 'bg-primary text-white' : 'bg-neutral-100 dark:bg-neutral-800 text-primary dark:text-primary-400 group-hover:bg-primary/20',
            compact ? 'w-6 h-6' : 'w-8 h-8'
          ]"
        >
          <i :class="`fa ${icon} ${compact ? 'text-sm' : 'text-lg'}`"></i>
        </div>
        <h3 :class="[
            'font-semibold truncate',
            isActive ? 'text-primary dark:text-primary-400' : 'text-neutral-800 dark:text-neutral-200',
            compact ? 'text-sm' : ''
          ]">{{ algorithm.name }}</h3>
      </div>
      
      <!-- 选中指示器 -->
      <div v-if="isActive" class="flex-shrink-0 ml-2">
        <div class="w-5 h-5 bg-primary rounded-full flex items-center justify-center text-white">
          <i class="fa fa-check text-xs"></i>
        </div>
      </div>
    </div>
    <p :class="[
        'truncate', 
        isActive ? 'text-neutral-700 dark:text-neutral-300' : 'text-neutral-600 dark:text-neutral-400', 
        compact ? 'text-xs' : 'text-sm'
      ]" 
      v-if="!compact || algorithm.needsKey">
      {{ compact ? '' : algorithm.description }}
    </p>
    <div v-if="algorithm.needsKey" :class="['flex items-center text-amber-600 dark:text-amber-400', compact ? 'text-xs mt-1' : 'text-xs mt-2']">
      <i class="fa fa-key mr-1"></i> 需要密钥
    </div>
    
    <!-- 使用TailwindCSS动画代替自定义CSS动画 -->
    <div v-if="isActive" class="absolute inset-0 bg-primary/5 pointer-events-none animate-pulse"></div>
    
    <!-- 悬停效果 -->
    <div class="absolute inset-0 bg-primary/5 opacity-0 group-hover:opacity-100 pointer-events-none transition-opacity duration-300" 
         v-if="!isActive"></div>
         
    <!-- 右侧选中指示条 -->
    <div v-if="isActive" class="absolute right-0 top-0 bottom-0 w-1 bg-primary"></div>
  </div>
</template>

<script>
export default {
  name: 'AlgorithmCard',
  props: {
    algorithm: {
      type: Object,
      required: true
    },
    isActive: {
      type: Boolean,
      default: false
    },
    compact: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    icon() {
      const type = this.algorithm.type;
      switch (type) {
        case 'encoding':
          return 'fa-exchange-alt';
        case 'symmetric':
          return 'fa-lock';
        case 'hash':
          return 'fa-hashtag';
        case 'hmac':
          return 'fa-shield-alt';
        case 'special':
          return 'fa-key';
        default:
          return 'fa-code';
      }
    }
  }
};
</script> 