<template>
  <div class="space-y-6">
    <!-- 输入区域 -->
    <div>
      <label class="block mb-2 font-semibold text-neutral-700 dark:text-neutral-200">
        <i class="fa fa-file-alt mr-2 text-primary"></i>原文
      </label>
      <div class="relative">
        <textarea 
          v-model="input" 
          class="w-full h-32 p-3 rounded-lg border-2 border-neutral-200 dark:border-neutral-700 bg-neutral-50 dark:bg-neutral-900 text-neutral-800 dark:text-neutral-100 font-mono focus:border-primary dark:focus:border-primary-400 focus:ring-2 focus:ring-primary/20 transition-all"
          placeholder="请输入要处理的文本"
        ></textarea>
        <button 
          v-if="input" 
          @click="input = ''" 
          class="absolute top-2 right-2 text-neutral-400 hover:text-neutral-600 dark:hover:text-neutral-300"
          title="清空输入"
        >
          <i class="fa fa-times-circle"></i>
        </button>
      </div>
    </div>
    
    <!-- 密钥输入 -->
    <div v-if="needsKey" class="animate-fade-in">
      <label class="block mb-2 font-semibold text-neutral-700 dark:text-neutral-200">
        <i class="fa fa-key mr-2 text-amber-500"></i>{{ keyLabel }}
      </label>
      <div class="relative">
        <input 
          v-model="cryptoKey" 
          class="w-full p-3 rounded-lg border-2 border-neutral-200 dark:border-neutral-700 bg-neutral-50 dark:bg-neutral-900 text-neutral-800 dark:text-neutral-100 font-mono focus:border-amber-500 focus:ring-2 focus:ring-amber-500/20 transition-all" 
          :placeholder="keyPlaceholder" 
        />
        <div class="absolute right-2 top-1/2 transform -translate-y-1/2 flex items-center">
          <span 
            v-if="['aes'].includes(algorithm) && cryptoKey" 
            :class="[
              'px-1.5 py-0.5 text-xs rounded mr-2',
              isValidKeyLength ? 'bg-green-100 text-green-700 dark:bg-green-900 dark:text-green-400' : 'bg-red-100 text-red-700 dark:bg-red-900 dark:text-red-400'
            ]"
          >
            {{ cryptoKey.length }}字符
          </span>
        </div>
      </div>
      <p v-if="algorithm === 'aes'" class="mt-1 text-xs text-neutral-500 dark:text-neutral-400">
        AES密钥长度必须是16、24或32个字符
      </p>
    </div>
    
    <!-- 加密模式选择 -->
    <div v-if="showModeOptions && ['aes', 'des'].includes(algorithm)" class="animate-fade-in">
      <label class="block mb-2 font-semibold text-neutral-700 dark:text-neutral-200">
        <i class="fa fa-cog mr-2 text-blue-500"></i>加密模式
      </label>
      <div class="grid grid-cols-5 gap-2">
        <button
          v-for="mode in ['ECB', 'CBC', 'CFB', 'OFB', 'CTR']"
          :key="mode"
          @click="cryptoOptions.mode = mode"
          class="p-2 text-center rounded-lg border-2 transition-all"
          :class="cryptoOptions.mode === mode 
            ? 'border-blue-500 bg-blue-50 text-blue-700 dark:border-blue-600 dark:bg-blue-900/30 dark:text-blue-400' 
            : 'border-neutral-200 hover:border-blue-300 dark:border-neutral-700 dark:hover:border-blue-800'"
        >
          {{ mode }}
        </button>
      </div>
      <p class="mt-1 text-xs text-neutral-500 dark:text-neutral-400">
        {{ getModeDescription(cryptoOptions.mode) }}
      </p>
    </div>
    
    <!-- 操作按钮 -->
    <div class="flex flex-wrap gap-3 pt-2">
      <button 
        v-if="!isHashMode && algorithm !== 'jwt'"
        @click="encrypt" 
        class="px-5 py-2.5 rounded-lg bg-primary text-white font-semibold hover:bg-primary/90 transition flex items-center shadow-md hover:shadow-lg"
      >
        <i class="fa fa-lock mr-2"></i> 加密
      </button>
      <button 
        v-if="!isHashMode && algorithm !== 'jwt'"
        @click="decrypt" 
        class="px-5 py-2.5 rounded-lg bg-secondary text-white font-semibold hover:bg-secondary/90 transition flex items-center shadow-md hover:shadow-lg"
      >
        <i class="fa fa-unlock mr-2"></i> 解密
      </button>
      <button 
        v-if="isHashMode" 
        @click="calculateHash" 
        class="px-5 py-2.5 rounded-lg bg-green-600 text-white font-semibold hover:bg-green-700 transition flex items-center shadow-md hover:shadow-lg"
      >
        <i class="fa fa-hashtag mr-2"></i> 计算哈希
      </button>
      <button 
        v-if="algorithm === 'jwt'" 
        @click="decodeJwt" 
        class="px-5 py-2.5 rounded-lg bg-blue-600 text-white font-semibold hover:bg-blue-700 transition flex items-center shadow-md hover:shadow-lg"
      >
        <i class="fa fa-key mr-2"></i> 解析JWT
      </button>
      <button 
        @click="clearAll" 
        class="px-5 py-2.5 rounded-lg bg-neutral-200 dark:bg-neutral-700 text-neutral-700 dark:text-neutral-200 font-semibold hover:bg-neutral-300 dark:hover:bg-neutral-600 transition flex items-center"
      >
        <i class="fa fa-trash-alt mr-2"></i> 清空
      </button>
    </div>
    
    <!-- 结果区域 -->
    <div>
      <label class="block mb-2 font-semibold text-neutral-700 dark:text-neutral-200">
        <i class="fa fa-clipboard-check mr-2 text-green-600"></i>结果
      </label>
      <div class="relative">
        <textarea
          v-if="algorithm !== 'jwt' || !jwtDecoded"
          readonly
          class="w-full h-32 p-3 rounded-lg border-2 border-neutral-200 dark:border-neutral-700 bg-neutral-50 dark:bg-neutral-900 text-neutral-800 dark:text-neutral-100 font-mono select-all"
          :value="output"
          placeholder="处理结果将显示在这里"
        ></textarea>
        
        <!-- JWT解析结果 -->
        <div
          v-else
          class="w-full p-3 rounded-lg border-2 border-neutral-200 dark:border-neutral-700 bg-neutral-50 dark:bg-neutral-900 text-neutral-800 dark:text-neutral-100 font-mono select-all text-xs whitespace-pre-wrap break-all"
          style="min-height:8rem; max-height:16rem; overflow-y:auto;"
        >
          <div v-if="jwtDecoded.error" class="text-red-500 mb-2 flex items-center">
            <i class="fa fa-exclamation-circle mr-1"></i> {{ jwtDecoded.error }}
          </div>
          <div class="mb-1 text-blue-600 dark:text-blue-400 font-bold">Header:</div>
          <pre class="mb-3 p-2 bg-blue-50 dark:bg-blue-900/30 rounded overflow-x-auto">{{ jwtDecoded.header }}</pre>
          <div class="mb-1 text-green-600 dark:text-green-400 font-bold">Payload:</div>
          <pre class="mb-3 p-2 bg-green-50 dark:bg-green-900/30 rounded overflow-x-auto">{{ jwtDecoded.payload }}</pre>
          <div class="mb-1 text-amber-600 dark:text-amber-400 font-bold">Signature:</div>
          <pre class="p-2 bg-amber-50 dark:bg-amber-900/30 rounded overflow-x-auto">{{ jwtDecoded.signature }}</pre>
        </div>
        
        <!-- 复制按钮 -->
        <div class="mt-2 flex items-center">
          <button 
            v-if="output || jwtDecoded" 
            @click="copyResult" 
            class="px-3 py-1.5 rounded-lg bg-green-500 text-white text-sm hover:bg-green-600 flex items-center shadow transition-all"
          >
            <i class="fa fa-copy mr-1.5"></i> 复制结果
          </button>
          <span v-if="copied" class="text-green-500 text-sm ml-2 flex items-center animate-fade-in">
            <i class="fa fa-check mr-1"></i> 已复制！
          </span>
        </div>
      </div>
    </div>
    
    <!-- 加载状态 -->
    <div v-if="loading" class="text-center py-4 animate-fade-in">
      <span class="text-primary dark:text-primary-400 flex items-center justify-center text-lg">
        <i class="fa fa-circle-notch fa-spin mr-2"></i> 处理中...
      </span>
    </div>
    
    <!-- 错误信息 -->
    <div v-if="errorMessage" class="bg-red-100 dark:bg-red-900/50 border-l-4 border-red-500 text-red-700 dark:text-red-300 p-4 rounded-r animate-fade-in">
      <div class="flex">
        <div class="flex-shrink-0">
          <i class="fa fa-exclamation-triangle text-red-500"></i>
        </div>
        <div class="ml-3">
          <p class="text-sm">{{ errorMessage }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CryptoService from '@/services/CryptoService';

export default {
  name: 'CryptoForm',
  props: {
    algorithm: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      input: '',
      output: '',
      cryptoKey: '',
      cryptoOptions: {
        mode: 'ECB',
        padding: 'Pkcs7'
      },
      copied: false,
      loading: false,
      errorMessage: '',
      showModeOptions: false,
      jwtDecoded: null
    };
  },
  computed: {
    needsKey() {
      return ['aes', 'des', 'rc4', 'hmacSha256'].includes(this.algorithm);
    },
    keyLabel() {
      if (this.algorithm === 'aes') return '密钥 (16/24/32字符)';
      if (this.algorithm === 'des') return '密钥';
      if (this.algorithm === 'rc4') return '密钥';
      if (this.algorithm === 'hmacSha256') return '密钥';
      return '密钥';
    },
    keyPlaceholder() {
      if (this.algorithm === 'aes') return '请输入16/24/32位密钥';
      if (this.algorithm === 'des') return '请输入密钥';
      if (this.algorithm === 'rc4') return '请输入密钥';
      if (this.algorithm === 'hmacSha256') return '请输入密钥';
      return '请输入密钥';
    },
    isHashMode() {
      return ['md5', 'sha1', 'sha256', 'sha512', 'hmacSha256'].includes(this.algorithm);
    },
    isValidKeyLength() {
      if (this.algorithm === 'aes') {
        return [16, 24, 32].includes(this.cryptoKey.length);
      }
      return true;
    }
  },
  watch: {
    algorithm: {
      handler(newAlgo) {
        // 切换算法时重置输出和错误信息
        this.output = '';
        this.errorMessage = '';
        this.jwtDecoded = null;
        this.showModeOptions = ['aes', 'des'].includes(newAlgo);
      },
      immediate: true
    }
  },
  methods: {
    async encrypt() {
      if (this.isHashMode) {
        return this.calculateHash();
      }

      this.errorMessage = '';
      this.loading = true;
      this.jwtDecoded = null;
      
      try {
        switch (this.algorithm) {
          case 'base64':
            this.output = CryptoService.base64Encode(this.input);
            break;
          case 'aes':
            this.output = await CryptoService.aesEncrypt(this.input, this.cryptoKey, this.cryptoOptions);
            break;
          case 'des':
            this.output = await CryptoService.desEncrypt(this.input, this.cryptoKey, this.cryptoOptions);
            break;
          case 'rc4':
            this.output = await CryptoService.rc4Encrypt(this.input, this.cryptoKey);
            break;
        }
      } catch (error) {
        this.errorMessage = error.message || '处理失败';
        this.output = '';
      } finally {
        this.loading = false;
      }
    },
    
    async decrypt() {
      if (this.isHashMode) return;
      
      this.errorMessage = '';
      this.loading = true;
      this.jwtDecoded = null;
      
      try {
        switch (this.algorithm) {
          case 'base64':
            this.output = CryptoService.base64Decode(this.input);
            break;
          case 'aes':
            this.output = await CryptoService.aesDecrypt(this.input, this.cryptoKey, this.cryptoOptions);
            break;
          case 'des':
            this.output = await CryptoService.desDecrypt(this.input, this.cryptoKey, this.cryptoOptions);
            break;
          case 'rc4':
            this.output = await CryptoService.rc4Decrypt(this.input, this.cryptoKey);
            break;
        }
      } catch (error) {
        this.errorMessage = error.message || '处理失败';
        this.output = '';
      } finally {
        this.loading = false;
      }
    },
    
    async calculateHash() {
      this.errorMessage = '';
      this.loading = true;
      this.jwtDecoded = null;
      
      try {
        switch (this.algorithm) {
          case 'md5':
            this.output = await CryptoService.md5Hash(this.input);
            break;
          case 'sha1':
            this.output = await CryptoService.sha1Hash(this.input);
            break;
          case 'sha256':
            this.output = await CryptoService.sha256Hash(this.input);
            break;
          case 'sha512':
            this.output = await CryptoService.sha512Hash(this.input);
            break;
          case 'hmacSha256':
            if (!this.cryptoKey) {
              throw new Error('请输入密钥');
            }
            this.output = await CryptoService.hmacSha256(this.input, this.cryptoKey);
            break;
        }
      } catch (error) {
        this.errorMessage = error.message || '处理失败';
        this.output = '';
      } finally {
        this.loading = false;
      }
    },
    
    decodeJwt() {
      this.errorMessage = '';
      this.loading = true;
      
      try {
        this.jwtDecoded = CryptoService.decodeJwt(this.input);
        if (this.jwtDecoded.error) {
          this.errorMessage = this.jwtDecoded.error;
        } else {
          this.output = '解析成功';
        }
      } catch (error) {
        this.errorMessage = error.message || '解析失败';
        this.output = '';
        this.jwtDecoded = null;
      } finally {
        this.loading = false;
      }
    },
    
    clearAll() {
      this.input = '';
      this.output = '';
      this.cryptoKey = '';
      this.copied = false;
      this.errorMessage = '';
      this.jwtDecoded = null;
    },
    
    copyResult() {
      let textToCopy = this.output;
      
      if (this.algorithm === 'jwt' && this.jwtDecoded) {
        textToCopy = `Header:\n${this.jwtDecoded.header}\n\nPayload:\n${this.jwtDecoded.payload}\n\nSignature:\n${this.jwtDecoded.signature}`;
      }
      
      navigator.clipboard.writeText(textToCopy).then(() => {
        this.copied = true;
        setTimeout(() => (this.copied = false), 1500);
      });
    },
    
    getModeDescription(mode) {
      const descriptions = {
        'ECB': '电子密码本模式 - 简单但安全性较低',
        'CBC': '密码块链接模式 - 每个块与前一个密文块异或',
        'CFB': '密码反馈模式 - 将前一个密文块加密后与当前明文异或',
        'OFB': '输出反馈模式 - 类似CFB，但使用加密输出进行反馈',
        'CTR': '计数器模式 - 加密递增计数器与明文异或'
      };
      return descriptions[mode] || '';
    }
  }
};
</script>

<style scoped>
/* 使用Tailwind CSS替代所有自定义样式 */
</style> 