<template>
  <main class="container mx-auto px-4 py-8 max-w-6xl">
    <!-- 标题区域 -->
    <div class="mb-8 flex items-center bg-gradient-to-r from-primary/10 to-transparent p-4 rounded-lg">
      <div class="h-14 w-14 flex items-center justify-center bg-primary rounded-full shadow-lg mr-4">
        <i class="fa fa-lock text-3xl text-white"></i>
      </div>
      <div>
        <h1 class="text-2xl md:text-3xl font-bold text-primary dark:text-primary-400 mb-1">文本加解密工具</h1>
        <p class="text-neutral-600 dark:text-neutral-300 text-base">
          支持 Base64、AES、DES、RC4、MD5/SHA 等多种加密与解密，安全便捷。
        </p>
      </div>
    </div>
    
    <!-- 主要内容区域 -->
    <div class="grid grid-cols-1 lg:grid-cols-12 gap-6">
      <!-- 左侧算法选择 -->
      <div class="lg:col-span-4 md:sticky md:top-4 md:self-start md:max-h-[calc(100vh-2rem)] overflow-hidden">
        <div class="bg-white dark:bg-neutral-800 rounded-xl shadow-lg border border-neutral-100 dark:border-neutral-700 p-4 md:p-5 max-h-full overflow-auto scrollbar-hide">
          <algorithm-selector 
            :modelValue="selectedAlgorithm" 
            @help="showHelp = true" 
            @update:modelValue="handleAlgorithmChange"
            key="algo-selector"
            ref="algoSelector"
          />
        </div>
      </div>
      
      <!-- 右侧表单操作 -->
      <div class="lg:col-span-8">
        <div class="bg-white dark:bg-neutral-800 rounded-xl shadow-lg border border-neutral-100 dark:border-neutral-700 p-4 md:p-6">
          <crypto-form 
            :algorithm="selectedAlgorithm" 
            ref="cryptoForm"
          />
        </div>
      </div>
    </div>
    
    <!-- 帮助对话框 -->
    <help-dialog :show="showHelp" title="文本加解密工具 - 使用说明" @close="showHelp = false">
      <crypto-help />
    </help-dialog>
  </main>
</template>

<script>
import AlgorithmSelector from '@/components/crypto/AlgorithmSelector.vue';
import CryptoForm from '@/components/crypto/CryptoForm.vue';
import CryptoHelp from '@/components/crypto/CryptoHelp.vue';
import HelpDialog from '@/components/HelpDialog.vue';

export default {
  name: 'TextCryptoPage',
  components: {
    AlgorithmSelector,
    CryptoForm,
    CryptoHelp,
    HelpDialog
  },
  data() {
    return {
      selectedAlgorithm: 'base64',
      showHelp: false
    };
  },
  mounted() {
    // 确保组件挂载时只有一个折叠面板打开
    this.$nextTick(() => {
      if (this.$refs.algoSelector) {
        this.$refs.algoSelector.$forceUpdate();
      }
    });
  },
  methods: {
    handleAlgorithmChange(newAlgorithm) {
      this.selectedAlgorithm = newAlgorithm;
      
      // 确保表单组件正确更新
      if (this.$refs.cryptoForm) {
        this.$nextTick(() => {
          this.$refs.cryptoForm.$forceUpdate();
        });
      }
    }
  }
};
</script>
