<template>
  <div>
    <div class="mb-4">
      <div class="flex items-center justify-between mb-2">
        <h3 class="text-lg font-semibold text-neutral-700 dark:text-neutral-200">选择加密算法</h3>
        <button 
          @click="$emit('help')" 
          class="text-primary dark:text-primary-400 hover:underline text-sm flex items-center"
        >
          <i class="fa fa-question-circle mr-1"></i> 使用帮助
        </button>
      </div>
      
      <!-- 搜索框 -->
      <div class="relative mb-3">
        <input 
          type="text" 
          v-model="searchQuery" 
          placeholder="搜索算法..." 
          class="w-full px-3 py-2 rounded-lg border border-neutral-200 dark:border-neutral-700 dark:bg-neutral-800 dark:text-neutral-200 focus:outline-none focus:ring-1 focus:ring-primary"
        />
        <i class="fa fa-search absolute right-3 top-1/2 transform -translate-y-1/2 text-neutral-400"></i>
      </div>
      
      <!-- 分类标签切换 - 水平滚动条 -->
      <div class="overflow-hidden">
        <div class="flex overflow-x-auto pb-1 mb-1 scrollbar-hide">
          <div class="flex gap-2 whitespace-nowrap">
            <button 
              v-for="category in categories" 
              :key="category.value"
              @click="activateCategory(category.value)"
              class="px-3 py-1 rounded-full text-sm transition-all duration-200 flex-shrink-0 relative select-none"
              :class="[
                activeCategory === category.value 
                ? 'bg-primary text-white' 
                : 'bg-neutral-100 dark:bg-neutral-800 text-neutral-700 dark:text-neutral-300',
                'active:scale-95'
              ]"
            >
              {{ category.name }}
              <!-- 选中指示线 -->
              <span 
                v-if="activeCategory === category.value"
                class="absolute bottom-[-2px] left-1/2 transform -translate-x-1/2 w-[70%] h-0.5 bg-current opacity-70"
              ></span>
            </button>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 分类折叠面板 - 改进的手风琴效果 -->
    <div v-if="searchQuery === ''" class="space-y-2 overflow-hidden">
      <!-- 编码/解码分类 -->
      <div class="mb-2 rounded-lg overflow-hidden border border-transparent transition-all duration-300"
           :class="{'shadow-sm border-neutral-200 dark:border-neutral-700': expandedCategory === 'encoding'}">
        <div 
          v-if="getCategoryAlgorithms('encoding').length > 0" 
          @click="toggleCategory('encoding')" 
          class="flex items-center justify-between p-2 rounded-lg cursor-pointer transition-colors duration-200 active:opacity-80"
          :class="expandedCategory === 'encoding' 
            ? 'bg-neutral-100 dark:bg-neutral-700 rounded-b-none' 
            : 'bg-neutral-50 dark:bg-neutral-800 hover:bg-neutral-100 dark:hover:bg-neutral-750'"
        >
          <div class="flex items-center">
            <i class="fa fa-exchange-alt text-primary dark:text-primary-400 mr-2"></i>
            <span class="font-medium">编码/解码</span>
            <span class="ml-2 text-xs bg-neutral-200 dark:bg-neutral-600 text-neutral-700 dark:text-neutral-300 rounded-full px-2">
              {{ getCategoryAlgorithms('encoding').length }}
            </span>
          </div>
          <i :class="`fa fa-chevron-${expandedCategory === 'encoding' ? 'up' : 'down'} text-sm text-neutral-500 transition-transform duration-300`"></i>
        </div>
        <div 
          v-show="expandedCategory === 'encoding'" 
          class="pl-2 pr-1 pt-1 pb-2 bg-white dark:bg-neutral-800 rounded-b-lg animate-fade-in"
        >
          <div class="grid grid-cols-1 gap-2 max-h-64 overflow-y-auto overflow-x-hidden scrollbar-hide pr-1">
            <algorithm-card
              v-for="algorithm in getCategoryAlgorithms('encoding')"
              :key="algorithm.value"
              :algorithm="algorithm"
              :is-active="modelValue === algorithm.value"
              @select="selectAlgorithm(algorithm.value)"
              class="compact"
            />
          </div>
        </div>
      </div>
      
      <!-- 对称加密分类 -->
      <div class="mb-2 rounded-lg overflow-hidden border border-transparent transition-all duration-300"
           :class="{'shadow-sm border-neutral-200 dark:border-neutral-700': expandedCategory === 'symmetric'}">
        <div 
          v-if="getCategoryAlgorithms('symmetric').length > 0" 
          @click="toggleCategory('symmetric')" 
          class="flex items-center justify-between p-2 rounded-lg cursor-pointer transition-colors duration-200 active:opacity-80"
          :class="expandedCategory === 'symmetric' 
            ? 'bg-neutral-100 dark:bg-neutral-700 rounded-b-none' 
            : 'bg-neutral-50 dark:bg-neutral-800 hover:bg-neutral-100 dark:hover:bg-neutral-750'"
        >
          <div class="flex items-center">
            <i class="fa fa-lock text-primary dark:text-primary-400 mr-2"></i>
            <span class="font-medium">对称加密</span>
            <span class="ml-2 text-xs bg-neutral-200 dark:bg-neutral-600 text-neutral-700 dark:text-neutral-300 rounded-full px-2">
              {{ getCategoryAlgorithms('symmetric').length }}
            </span>
          </div>
          <i :class="`fa fa-chevron-${expandedCategory === 'symmetric' ? 'up' : 'down'} text-sm text-neutral-500 transition-transform duration-300`"></i>
        </div>
        <div 
          v-show="expandedCategory === 'symmetric'" 
          class="pl-2 pr-1 pt-1 pb-2 bg-white dark:bg-neutral-800 rounded-b-lg animate-fade-in"
        >
          <div class="grid grid-cols-1 gap-2 max-h-64 overflow-y-auto overflow-x-hidden scrollbar-hide pr-1">
            <algorithm-card
              v-for="algorithm in getCategoryAlgorithms('symmetric')"
              :key="algorithm.value"
              :algorithm="algorithm"
              :is-active="modelValue === algorithm.value"
              @select="selectAlgorithm(algorithm.value)"
              class="compact"
            />
          </div>
        </div>
      </div>
      
      <!-- 哈希算法分类 -->
      <div class="mb-2 rounded-lg overflow-hidden border border-transparent transition-all duration-300"
           :class="{'shadow-sm border-neutral-200 dark:border-neutral-700': expandedCategory === 'hash'}">
        <div 
          v-if="getCategoryAlgorithms('hash').length > 0" 
          @click="toggleCategory('hash')" 
          class="flex items-center justify-between p-2 rounded-lg cursor-pointer transition-colors duration-200 active:opacity-80"
          :class="expandedCategory === 'hash' 
            ? 'bg-neutral-100 dark:bg-neutral-700 rounded-b-none' 
            : 'bg-neutral-50 dark:bg-neutral-800 hover:bg-neutral-100 dark:hover:bg-neutral-750'"
        >
          <div class="flex items-center">
            <i class="fa fa-hashtag text-primary dark:text-primary-400 mr-2"></i>
            <span class="font-medium">哈希算法</span>
            <span class="ml-2 text-xs bg-neutral-200 dark:bg-neutral-600 text-neutral-700 dark:text-neutral-300 rounded-full px-2">
              {{ getCategoryAlgorithms('hash').length }}
            </span>
          </div>
          <i :class="`fa fa-chevron-${expandedCategory === 'hash' ? 'up' : 'down'} text-sm text-neutral-500 transition-transform duration-300`"></i>
        </div>
        <div 
          v-show="expandedCategory === 'hash'" 
          class="pl-2 pr-1 pt-1 pb-2 bg-white dark:bg-neutral-800 rounded-b-lg animate-fade-in"
        >
          <div class="grid grid-cols-1 gap-2 max-h-64 overflow-y-auto overflow-x-hidden scrollbar-hide pr-1">
            <algorithm-card
              v-for="algorithm in getCategoryAlgorithms('hash')"
              :key="algorithm.value"
              :algorithm="algorithm"
              :is-active="modelValue === algorithm.value"
              @select="selectAlgorithm(algorithm.value)"
              class="compact"
            />
          </div>
        </div>
      </div>
      
      <!-- 特殊功能分类 -->
      <div class="mb-2 rounded-lg overflow-hidden border border-transparent transition-all duration-300"
           :class="{'shadow-sm border-neutral-200 dark:border-neutral-700': expandedCategory === 'special'}">
        <div 
          v-if="getCategoryAlgorithms('special').length > 0" 
          @click="toggleCategory('special')" 
          class="flex items-center justify-between p-2 rounded-lg cursor-pointer transition-colors duration-200 active:opacity-80"
          :class="expandedCategory === 'special' 
            ? 'bg-neutral-100 dark:bg-neutral-700 rounded-b-none' 
            : 'bg-neutral-50 dark:bg-neutral-800 hover:bg-neutral-100 dark:hover:bg-neutral-750'"
        >
          <div class="flex items-center">
            <i class="fa fa-key text-primary dark:text-primary-400 mr-2"></i>
            <span class="font-medium">特殊功能</span>
            <span class="ml-2 text-xs bg-neutral-200 dark:bg-neutral-600 text-neutral-700 dark:text-neutral-300 rounded-full px-2">
              {{ getCategoryAlgorithms('special').length }}
            </span>
          </div>
          <i :class="`fa fa-chevron-${expandedCategory === 'special' ? 'up' : 'down'} text-sm text-neutral-500 transition-transform duration-300`"></i>
        </div>
        <div 
          v-show="expandedCategory === 'special'" 
          class="pl-2 pr-1 pt-1 pb-2 bg-white dark:bg-neutral-800 rounded-b-lg animate-fade-in"
        >
          <div class="grid grid-cols-1 gap-2 max-h-64 overflow-y-auto overflow-x-hidden scrollbar-hide pr-1">
            <algorithm-card
              v-for="algorithm in getCategoryAlgorithms('special')"
              :key="algorithm.value"
              :algorithm="algorithm"
              :is-active="modelValue === algorithm.value"
              @select="selectAlgorithm(algorithm.value)"
              class="compact"
            />
          </div>
        </div>
      </div>
    </div>
    
    <!-- 搜索结果 -->
    <div v-else-if="filteredAlgorithms.length > 0" class="grid grid-cols-1 gap-3 overflow-hidden animate-fade-in">
      <algorithm-card
        v-for="algorithm in filteredAlgorithms"
        :key="algorithm.value"
        :algorithm="algorithm"
        :is-active="modelValue === algorithm.value"
        @select="selectAlgorithm(algorithm.value)"
      />
    </div>
    
    <!-- 无搜索结果提示 -->
    <div v-else class="text-center py-6 text-neutral-500 dark:text-neutral-400 animate-fade-in">
      <i class="fa fa-search text-3xl mb-2 opacity-50"></i>
      <p>未找到匹配的算法</p>
    </div>
  </div>
</template>

<script>
import CryptoService from '@/services/CryptoService';
import AlgorithmCard from './AlgorithmCard.vue';

export default {
  name: 'AlgorithmSelector',
  components: {
    AlgorithmCard
  },
  props: {
    modelValue: {
      type: String,
      default: 'base64'
    }
  },
  emits: ['update:modelValue', 'help'],
  data() {
    return {
      algorithms: [],
      activeCategory: 'all',
      searchQuery: '',
      expandedCategory: 'encoding', // 只存储当前展开的分类，默认展开编码分类
      categories: [
        { value: 'all', name: '全部' },
        { value: 'encoding', name: '编码/解码' },
        { value: 'symmetric', name: '对称加密' },
        { value: 'hash', name: '哈希算法' },
        { value: 'special', name: '特殊功能' }
      ]
    };
  },
  computed: {
    filteredAlgorithms() {
      if (this.searchQuery) {
        return this.algorithms.filter(algo => 
          algo.name.toLowerCase().includes(this.searchQuery.toLowerCase()) || 
          algo.description.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      }
      
      if (this.activeCategory === 'all') {
        return this.algorithms;
      }
      return this.algorithms.filter(algo => algo.type === this.activeCategory);
    }
  },
  created() {
    this.algorithms = CryptoService.getSupportedAlgorithms();
  },
  methods: {
    selectAlgorithm(value) {
      this.$emit('update:modelValue', value);
      
      // 强制更新视图，确保选中状态立即反映
      this.$nextTick(() => {
        this.$forceUpdate();
      });
    },
    getCategoryAlgorithms(categoryType) {
      // 获取指定分类下的算法列表
      return this.algorithms.filter(algo => algo.type === categoryType);
    },
    toggleCategory(categoryValue) {
      // 切换折叠面板，实现手风琴效果
      this.expandedCategory = this.expandedCategory === categoryValue ? '' : categoryValue;
    },
    activateCategory(categoryValue) {
      if (this.activeCategory !== categoryValue) {
        this.activeCategory = categoryValue;
        
        // 如果选择了特定分类，自动展开该分类
        if (categoryValue !== 'all') {
          this.expandedCategory = categoryValue;
        } else {
          // 全部分类时，默认展开第一个有内容的分类
          const firstCategory = ['encoding', 'symmetric', 'hash', 'special'].find(
            type => this.getCategoryAlgorithms(type).length > 0
          );
          if (firstCategory) {
            this.expandedCategory = firstCategory;
          }
        }
      }
    }
  },
  watch: {
    // 监听外部传入的modelValue变化，确保组件状态一致
    modelValue: {
      immediate: true,
      handler(newValue) {
        if (newValue) {
          // 找到当前选中算法所在的分类并展开
          const selectedAlgo = this.algorithms.find(a => a.value === newValue);
          if (selectedAlgo) {
            const category = selectedAlgo.type;
            if (category) {
              this.expandedCategory = category;
            }
          }
        }
      }
    }
  }
};
</script> 